import React from 'react';
import {SURVEY} from "./survey";
import {scrollToElement} from "./utils/scroll";
import $ from 'jquery';

function randomize(length) {
	const input = [];
	const output = [];
	for (let i = 0; i < length; ++i)
		input.push(i);
	while (input.length) {
		const index = Math.floor(Math.random() * input.length);
		const value = input.splice(index, 1);
		output.push(value[0]);
	}
	return output;
}

export class App extends React.Component {
	constructor(props) {
		super(props);
		this.state = {orders: SURVEY.map(entry => randomize(entry.images.length)), status: -1};
		this.onSelect = this.onSelect.bind(this);
		this.haveSelectedAll = this.haveSelectedAll.bind(this);
		this.submit = this.submit.bind(this);
		this.checkResult = this.checkResult.bind(this);
	}
	onSelect(entryIndex, modelIndex) {
		this.setState({[entryIndex]: modelIndex}, () => {
			const nextEntry = entryIndex + 1;
			if (nextEntry < SURVEY.length) {
				const nextId = `question-${nextEntry + 1}`;
				setTimeout(() => scrollToElement(nextId), 100);
			} else {
				setTimeout(() => scrollToElement('form'), 100);
			}
		});
	}
	haveSelectedAll() {
		for (let i = 0; i < SURVEY.length; ++i) {
			if (this.state[i] === undefined)
				return false;
		}
		return true;
	}
	submit() {
		const selectedModels = SURVEY.map((entry, entryIndex) => entry.models[this.state[entryIndex]]);
		const entryNames = SURVEY.map(entry => `case_${entry.entry}`);
		const form = {};
		for (let i = 0; i < SURVEY.length; ++i) {
			form[entryNames[i]] = selectedModels[i];
		}
		/** Google Sheet:
		 * https://docs.google.com/spreadsheets/d/1viSVdnEfMIyyzGQQt9-Gebr9O0NzaupqZKoCGvxzhew/edit#gid=0
		 * **/
		const url = 'https://script.google.com/macros/s/AKfycbw_XgfD3ww92HrK1OLlAg5xYEcp96XHuocZDvZpDNQO0EgHWKc/exec';
		this.setState({status: 0}, () => {
			$.ajax({
				url: url,
				method: "GET",
				dataType: "json",
				data: form,
				success: (d, s, j) => this.checkResult(d.result),
				error: (jq, msg, err) => this.checkResult('error')
			})
		});
	}
	checkResult(message) {
		if (message === 'success') {
			this.setState({status: 1},
				() => window.alert('Your choices have been submitted! Thanks!'));
		} else {
			this.setState({status: -1},
				() => window.alert('An internal error occurred, your choices have not been submitted. Sorry for inconvenient!'));
		}
	}
	render() {
		return (
			<div className="container page mt-2 mb-5">
				<div className="survey">
				{SURVEY.map((entry, entryIndex) => (
					<div key={entryIndex} className="mb-5 entry text-center" id={`question-${entryIndex + 1}`}>
						<h1>Question {entryIndex + 1}</h1>
						<div>
							<div className="original mb-4">
								<img className="img-fluid w-50" alt={'original'} src={entry.original}/>
							</div>
							<div className="row derived-images">
								{this.state.orders[entryIndex].map((modelIndex, localImgIndex) => {
									const character = String.fromCharCode('A'.charCodeAt(0) + localImgIndex);
									const selected = this.state[entryIndex] === modelIndex;
									return (
										<div className="col-md m-1 p-0 derived-image" key={modelIndex}>
											<div className="image-wrapper"
												 style={{backgroundImage: `url(${entry.images[modelIndex]})`}}>
												<div className={`image-number ${selected? 'selected' : 'not-selected'}`}
													 onClick={() => this.onSelect(entryIndex, modelIndex)}
													 title={`image ${character}`}>
													<div className="image-number-table">
														<div className="image-number-cell">{character}</div>
													</div>
												</div>
											</div>
										</div>
									);
								})}
							</div>
						</div>
					</div>
				))}
				</div>
				{this.state.status === 1 ? '' : (
					<div className="form" id="form">
						<button type="button"
								className="btn btn-success btn-block"
								onClick={this.submit}
								disabled={!this.haveSelectedAll() || this.state.status === 0}>
							<strong>{this.state.status === -1 ? 'submit' : 'submitting ...'}</strong>
						</button>
					</div>
				)}
			</div>
		);
	}
}

